@import "../../css/util/_all.scss";


.menu-bar {
  display: flex;
  flex-direction: row;
  color: $white;
  list-style: none;
  padding:0;
  margin:0;
  cursor: pointer;

  white-space: nowrap;
  overflow: hidden;


  @include viewports(up-to tiny) {
    display: none;
  }

  li {
    padding: 0 10px;
    &:hover {
      color: #ccc;
    }

  }
}
