p {
  font-size: 15px;
  line-height: 1.3; }
  @media screen and (min-width: 768px) {
    p {
      font-size: 16px; } }
  @media screen and (min-width: 1024px) {
    p {
      font-size: 17px;
      line-height: 1.4; } }
  @media screen and (min-width: 900px) {
    p {
      font-size: 18px; } }
  @media screen and (min-width: 1280px) {
    p {
      font-size: 19px;
      line-height: 1.45; } }
  @media screen and (min-width: 1440px) {
    p {
      font-size: 20px; } }

.test-container button {
  margin-right: 15px; }

.btn {
  border: 0;
  border-radius: 2px;
  color: #fff;
  background-color: #000;
  cursor: pointer;
  font-size: 20px;
  line-height: 1.3;
  padding: 5px 10px; }
  @media screen and (min-width: 768px) {
    .btn {
      font-size: 16px; } }
  @media screen and (min-width: 1024px) {
    .btn {
      font-size: 17px;
      line-height: 1.4; } }
  @media screen and (min-width: 900px) {
    .btn {
      font-size: 18px; } }
  @media screen and (min-width: 1280px) {
    .btn {
      font-size: 19px;
      line-height: 1.45; } }
  @media screen and (min-width: 1440px) {
    .btn {
      font-size: 20px; } }
  @media screen and (min-width: 768px) {
    .btn {
      padding: 5px 10px; } }
  @media screen and (min-width: 1024px) {
    .btn {
      padding: 30px 30px; } }
  .btn:hover {
    background: #fff;
    color: #000;
    -webkit-transition: all 0.3s;
    transition: all 0.3s; }
  .btn:active {
    -webkit-transform: translateY(3px);
            transform: translateY(3px); }
  .btn.round {
    border: 1px solid #8f8f8f;
    border-radius: 50%;
    background-color: transparent;
    padding: 10px;
    line-height: 0; }
    @media screen and (min-width: 768px) {
      .btn.round {
        padding: 15px; } }
    @media screen and (min-width: 1024px) {
      .btn.round {
        padding: 20px; } }
    .btn.round:hover {
      border-color: #fff;
      color: #fff;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      -webkit-transition: all 0.3s;
      transition: all 0.3s; }
    .btn.round svg.icon {
      width: 12px;
      height: 12px; }
      @media screen and (min-width: 480px) {
        .btn.round svg.icon {
          width: 12px; } }
      @media screen and (min-width: 768px) {
        .btn.round svg.icon {
          width: 18px; } }
      @media screen and (min-width: 1024px) {
        .btn.round svg.icon {
          width: 24px; } }
      @media screen and (min-width: 1280px) {
        .btn.round svg.icon {
          width: 24px; } }
      @media screen and (min-width: 480px) {
        .btn.round svg.icon {
          height: 12px; } }
      @media screen and (min-width: 768px) {
        .btn.round svg.icon {
          height: 18px; } }
      @media screen and (min-width: 1024px) {
        .btn.round svg.icon {
          height: 24px; } }
      @media screen and (min-width: 1280px) {
        .btn.round svg.icon {
          height: 24px; } }
  .btn.red {
    background-color: #c1071e;
    color: #fff; }
  .btn.hollow {
    background-color: transparent;
    border-color: 1px solid #8f8f8f; }
    .btn.hollow:hover {
      background-color: rgba(17, 17, 17, 0.67);
      color: white; }

